// next-app/pages/register.js
import { useState } from 'react';
import axios from '../services/api';
import { useRouter } from 'next/router';
import Link from 'next/link'; 

export default function Register() {
  const [username, setUsername] = useState('');
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [error, setError] = useState(''); // State to hold error messages
  const router = useRouter();

  const handleRegister = async () => {
    setError(''); // Clear previous errors
    try {
      const res = await axios.post('/auth/register', { username, email, password });
      console.log('Registration Success:', res.data); // Log success
      // If registration is successful, the backend should send a token/user data.
      // Based on our authController update, it sends `token` and `user` object.
      // You might want to automatically log in the user or just redirect.
      // For now, let's just redirect to login as per your original intent.

      // Optionally, if you want to automatically log in and store user:
      // localStorage.setItem('token', res.data.token);
      // localStorage.setItem('user', JSON.stringify(res.data.user)); // Store user object

      router.push('/login'); // Redirect to login page
    } catch (err) {
      console.error('Registration Failed:', err.response?.data || err.message);
      setError(err.response?.data?.error || 'Registration failed. Please try again.'); // Display specific error from backend or a generic one
    }
  };

  return (
    <div className="p-4 max-w-md mx-auto">
      <h2 className="text-xl font-bold">Register</h2>
      {error && <p className="text-red-500 mb-4">{error}</p>} {/* Display error message */}
      <input className="border p-2 w-full" value={username} onChange={e => setUsername(e.target.value)} placeholder="Username" />
      <input className="border p-2 w-full mt-2" value={email} onChange={e => setEmail(e.target.value)} placeholder="Email" />
      <input className="border p-2 w-full mt-2" type="password" value={password} onChange={e => setPassword(e.target.value)} placeholder="Password" />
      <button className="bg-green-600 text-white p-2 mt-4 w-full" onClick={handleRegister}>Register</button>
      <p className="mt-4 text-center">
        Already have an account?{' '}
        <Link href="/login" className="text-blue-500 hover:underline">
          Login
        </Link>
      </p>
    </div>
  );
}